/* chat.css */
body {
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #0a0a0a;
  color: #00ffcc;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.container {
  width: 100%;
  max-width: 600px;
  height: 90vh;
  padding: 2rem;
  background-color: #111;
  border-radius: 12px;
  box-shadow: 0 0 20px #00ffcc44;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.chat-box {
  flex: 1;
  overflow-y: auto;
  padding-right: 10px;
  margin-bottom: 1rem;
}

.bot-message,
.user-message {
  background-color: #222;
  border: 2px solid #00ffcc;
  border-radius: 8px;
  padding: 0.75rem 1rem;
  margin-bottom: 0.5rem;
  word-break: break-word;
}

.bot-message {
  color: #00ffaa;
}

.user-message {
  color: #ffffff;
  text-align: right;
  border-color: #00aaff;
}

.input-area {
  display: flex;
  gap: 0.5rem;
}

.input-area input[type="text"] {
  flex: 1;
  padding: 0.75rem;
  font-size: 1rem;
  background-color: #222;
  color: #00ffcc;
  border: 2px solid #00ffcc;
  border-radius: 8px;
}

.input-area button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.75rem 1rem;
  font-size: 1rem;
  font-weight: bold;
  background-color: #00ffcc;
  color: #000;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  box-shadow: 0 0 8px #00ffcc;
  transition: background-color 0.3s;
  white-space: nowrap;
}

.input-area button:hover {
  background-color: #00ffaa;
}

.submit-button {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
  margin-top: 1rem;
}

.submit-button button {
  flex: 1;
  background-color: #00ffcc;
  color: #000;
  border: none;
  padding: 0.75rem;
  font-size: 1rem;
  font-weight: bold;
  border-radius: 8px;
  cursor: pointer;
  box-shadow: 0 0 10px #00ffcc;
  transition: background-color 0.3s;
}

.submit-button button:hover {
  background-color: #00ffaa;
}
