/* index.css */
body {
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #0a0a0a;
  color: #00ffcc;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.container {
  width: 100%;
  max-width: 600px;
  padding: 2rem;
  background-color: #111;
  border-radius: 12px;
  box-shadow: 0 0 20px #00ffcc44;
}

.title {
  text-align: center;
  font-size: 2.5rem;
  color: #00ffcc;
  text-shadow: 0 0 10px #00ffcc;
}

.subtitle {
  text-align: center;
  font-size: 1rem;
  color: #ccc;
  margin-bottom: 2rem;
}

.section {
  margin-bottom: 2rem;
}

.section h2 {
  font-size: 1.2rem;
  margin-bottom: 0.5rem;
  color: #00ffaa;
}

.radio-group label {
  display: block;
  background-color: #222;
  border: 2px solid #00ffcc;
  border-radius: 8px;
  padding: 0.75rem 1rem;
  margin-bottom: 0.5rem;
  cursor: pointer;
  transition: background-color 0.3s;
}

.radio-group input[type="radio"] {
  display: none;
}

.radio-group label.selected,
.radio-group label:hover {
  background-color: #00ffcc33;
}

select,
input[type="text"] {
  width: 100%;
  padding: 0.75rem;
  font-size: 1rem;
  background-color: #222;
  color: #00ffcc;
  border: 2px solid #00ffcc;
  border-radius: 8px;
}

.example-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.example-group button {
  background-color: #111;
  color: #00ffcc;
  border: 2px solid #00ffcc;
  padding: 0.75rem 1rem;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s;
  text-align: left;
}

.example-group button:hover {
  background-color: #00ffcc22;
}

.submit-button {
  text-align: center;
  margin-top: 1.5rem;
}

.submit-button button {
  background-color: #00ffcc;
  color: #000;
  border: none;
  padding: 0.75rem 2rem;
  font-size: 1rem;
  font-weight: bold;
  border-radius: 8px;
  cursor: pointer;
  box-shadow: 0 0 10px #00ffcc;
  transition: background-color 0.3s, color 0.3s;
}

.submit-button button:hover {
  background-color: #00ffaa;
  color: #000;
}

footer {
  text-align: center;
  margin-top: 2rem;
  font-size: 0.9rem;
  color: #666;
}